import { Octokit } from '@octokit/core';
import './App.css';
import { useEffect, useState, useRef } from 'react';

export const ACCOUNT = () => {

    const [myvalue, setmyvalue] = useState(null);
    const [account, setaccount] = useState(null);
    const [situationdata, setsituationdata] = useState(null);
    const [addaccount, setaddaccount] = useState(false)

    const [accountinfo, setaccountinfo] = useState({});

    const [isadmin, useisadmin] = useState(false)

      // Fetching data from database
  const fetchFileContentFromRepository = async (path) => {
    try {
      const octokit = new Octokit({
        auth: process.env.REACT_APP_GITHUB_AUTH_TOKEN
      });
  
      const response = await octokit.request('GET /repos/{owner}/{repo}/contents/{path}', {
        owner: 'SajadKenani',
        repo: 'dentistdatabase',
        path: path 
      });
        const content = response.data.content;
        const decodedContent = decodeURIComponent(escape(atob(content)));

        setaccount(decodedContent);

    } catch (error) {
      console.error('Error fetching file content from repository:', error);
      return null;
    }
  }

    // Sending data to the database
    async function sendDataToRepository(data) {
      try {
        const octokit = new Octokit({
          auth: process.env.REACT_APP_GITHUB_AUTH_TOKEN
        });
        // getting the data
        const encoder = new TextEncoder();
        const uint8Array = encoder.encode(data);
        const response = await octokit.request('GET /repos/{owner}/{repo}/contents/{path}', {
          owner: 'SajadKenani',
          repo: 'dentistdatabase',
          path: 'accounts.js' 
        });

        // Update the database with the new data
        const sendingFile = await octokit.request('PUT /repos/{owner}/{repo}/contents/{path}', {
          owner: 'SajadKenani',
          repo: 'dentistdatabase',
          path: 'accounts.js',
          message: 'my commit message',
          committer: {
            name: 'Monalisa Octocat',
            email: 'octocat@github.com'
          },
          content: btoa(String.fromCharCode.apply(null, uint8Array)),
          sha: response.data.sha,
          headers: {
            'X-GitHub-Api-Version': '2022-11-28'
          },

        });
        
        console.log('Data sent to repository successfully:', sendingFile.data);
    
      } catch (error) {
        console.error('Error sending data to repository:', error);
      }
    }

    
    const deleteSpecificItem = async (index) => {
        const value = JSON.parse(account)
        value.splice(index, 1); // Corrected the splice parameters
        console.log(value);
        await sendDataToRepository(JSON.stringify(value)); // Wait for this function to complete
        fetchFileContentFromRepository("accounts.js");
      
      };

    const addNewAccount = async () => {
        const value = JSON.parse(account)
        value.push(accountinfo)
    
        await sendDataToRepository(JSON.stringify((value)))
        fetchFileContentFromRepository("accounts.js");
        console.log("done!")

        setaddaccount(!addaccount)
    }

    useEffect(() => {
   
        fetchFileContentFromRepository("accounts.js");

      }, [])


    useEffect(() => {
        const myFunction = async () => {
        console.log(JSON.parse(account))
     

        if (account){
          if(JSON.parse(account)[Number(localStorage.getItem("num"))].type === "super_admin"){
            useisadmin(true)
            console.log("sdf")
        }
    }
    }

    myFunction()
      }, [account])



    return (
        <div style={{marginLeft: "700px"}}>
{isadmin && <>
        {addaccount && 
        <div style={{width: "100%"}} className="flex items-center h-screen fixed ">
          <div className="max-w-md w-full p-8 bg-white rounded-lg shadow-lg">
            <h1 className="text-center text-3xl font-semibold mb-6">  </h1>
          
              <div className="mb-6">
                <input
                  type=""
                  className="w-full px-4 py-2 rounded border border-gray-300 focus:outline-none focus:border-blue-500"
                  placeholder=" "
                  onChange={(e) => { setaccountinfo(prevState => ({ ...prevState, "name": e.target.value  }))}}
                />
              </div>
              <div className="mb-6">
                <input
                  type=""
                  className="w-full px-4 py-2 rounded border border-gray-300 focus:outline-none focus:border-blue-500"
                  placeholder=" "
                  onChange={(e) => { setaccountinfo(prevState => ({ ...prevState, "email": e.target.value  }))}}
                />
              </div>
              <div className="mb-6">
                <input
                  type=""
                  className="w-full px-4 py-2 rounded border border-gray-300 focus:outline-none focus:border-blue-500"
                  placeholder=" "
                  onChange={(e) => { setaccountinfo(prevState => ({ ...prevState, "password": e.target.value  }))}}
                />
              </div>
              <div className="mb-6">
                <select className="w-full px-4 py-2 rounded border border-gray-300 focus:outline-none focus:border-blue-500" onChange={(e) => { setaccountinfo(prevState => ({ ...prevState, "type": e.target.value  }))}}>
                  <option value=""> </option>
                  <option value="side_admin">side_admin</option>
                  <option value="user">user</option>
                </select>
              </div>
              <div className="text-center">
                <button className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 focus:outline-none" onClick={addNewAccount}>
                   
                </button>
              </div>
           
          </div>
        </div>}

        {console.log(JSON.parse(account))}
        {account && JSON.parse(account).map((item, index) => (
         <div className="flex justify-center items-center min-h-screen " >
          <div className="p-8 bg-white rounded-lg shadow-lg">
            <h1 className="text-center text-3xl font-semibold mb-6"> </h1>
            <div className="overflow-x-auto">
              <table className="w-full table-auto">
                <thead>
                  <tr className="bg-gray-200">
                    <th className="px-4 py-2 text-left"></th>
                    <th className="px-4 py-2 text-left"> </th>
                    <th className="px-4 py-2 text-left"> </th>
                    <th className="px-4 py-2 text-left"> </th>
                    <th className="px-4 py-2 text-left"></th>
                  </tr>
                </thead>
                <tbody>
                  {account &&
                    JSON.parse(account).map((item, index) => (
                      <tr key={index} className={index % 2 === 0 ? 'bg-gray-100' : 'bg-white'}>
                        <td className="px-4 py-2">{item.name}</td>
                        <td className="px-4 py-2">{item.email}</td>
                        <td className="px-4 py-2">{item.password}</td>
                        <td className="px-4 py-2">{item.type}</td>
                        {item.type !== "super_admin" ? <td className="px-4 py-2 text-red-600 cursor-pointer hover:underline" onClick={() => deleteSpecificItem(index)}></td> : <td className="px-4 py-2 "></td>}
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        ))}
             <div style={{width: "150px", height: "120px", backgroundColor: "#333", margin: "20px", color: "white", display: "flex", alignItems: "center", justifyContent: "center", borderRadius: "5px", cursor: "pointer", bottom: "0", position: "fixed", right: "0"}} onClick={() => setaddaccount(!addaccount)}>
         
       </div>

  

       </>
}
    </div>
    )
}