import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import Add from './content';
import { useState } from 'react';
import {ACCOUNT} from "./account"
import { Octokit } from '@octokit/core';

function App() {
  const [situationdata, setsituationdata] = useState(null);
  const [account, setaccount] = useState(null);
  const [myemail, setmyemail] = useState(null);
  const [mypassword, setmypassword] = useState(null);
  const [accountinfo, setaccountinfo] = useState({});

  const [myvalue, setmyvalue] = useState(null);


  const [signed, setsigned] = useState(false);

    // Fetching data from database
    const fetchFileContentFromRepository = async (path) => {
      try {
        const octokit = new Octokit({
          auth: process.env.REACT_APP_GITHUB_AUTH_TOKEN
        });
    
        const response = await octokit.request('GET /repos/{owner}/{repo}/contents/{path}', {
          owner: 'SajadKenani',
          repo: 'dentistdatabase',
          path: path 
        });
          const content = response.data.content;
          const decodedContent = decodeURIComponent(escape(atob(content)));
          if (path === "data.js"){
          setmyvalue(decodedContent);
        }else if (path === "accounts.js"){
          setaccount(decodedContent);
        }else if(path === "situations.js"){
          setsituationdata(decodedContent);
        }
      } catch (error) {
        console.error('Error fetching file content from repository:', error);
        return null;
      }
    }
  

// sign in page
const handleSignIn = async () => {
  try {
    // Fetch account data from repository
    await fetchFileContentFromRepository("accounts.js");

    if (account) {

      for (let i = 0; i < account.length; i++) {
        if (myemail === JSON.parse(account)[i].email && mypassword === JSON.parse(account)[i].password) {

          setaccountinfo(JSON.parse(account)[i]);
          setsigned(true);
          localStorage.setItem("num", i)
          return; // Exit the loop early if the account is found
        }
      }

      console.log("Account not found");

    } else {
      console.log("No account data available");
    }
  } catch (error) {
    console.error("Error fetching account data:", error);
  }
};

  return (
<Router>
  <div className='flex'>
    {!signed &&  
      <div className='flex justify-center items-center h-screen' style={{width: "100%", display: "flex", justifyContent: "center"}}>
  <div className='bg-gray-200 p-8 rounded-lg shadow-lg' >
    <h1 className='text-center text-3xl font-semibold mb-6'> </h1>
    <div className='mb-6'>
      <input
        type='email'
        onChange={(e) => setmyemail(e.target.value)}
        className='w-full px-4 py-2 rounded border border-gray-400 focus:outline-none focus:border-blue-500'
        placeholder=' '
      />
    </div>
    <div className='mb-6'>
      <input
        type='password'
        onChange={(e) => setmypassword(e.target.value)}
        className='w-full px-4 py-2 rounded border border-gray-400 focus:outline-none focus:border-blue-500'
        placeholder=' '
      />
    </div>
    <div className='text-center'>
      <button
        onClick={handleSignIn}
        className='px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 focus:outline-none'
      >
         
      </button>
    </div>
  </div>
</div>

    }

    {signed && 
      <>
        <div style={{ width: '10%', height: '100vh', backgroundColor: '#2c3e50', position: 'fixed', top: 0, left: 0 }}>
          <div className='mt-20 text-center'>
            <div style={{ width: '120px', height: '120px', backgroundColor: '#e74c3c', borderRadius: '50%', margin: '0 auto' }}></div>
          </div>
          <div className='mt-4' style={{ cursor: "pointer" }}>
            <div className='text-center text-white'>  <Link to={"/"}> </Link></div>
            <div style={{ width: '120px', height: '3px', backgroundColor: '#e74c3c', margin: '0 auto' }}></div>
          </div>
          <div className='mt-4' style={{ cursor: "pointer" }}>
            <div className='text-center text-white'></div>
            <div style={{ width: '120px', height: '3px', backgroundColor: '#e74c3c', margin: '0 auto' }}></div>
          </div>
          
          {accountinfo.type === "super_admin" && 
            <div className='mt-4' style={{ cursor: "pointer" }}>
              <div className='text-center text-white'></div>
              <div style={{ width: '120px', height: '3px', backgroundColor: '#e74c3c', margin: '0 auto' }}></div>
            </div>
          }

          <div className='mt-4' style={{ cursor: "pointer" }}>
            <div className='text-center text-white'></div>
            <div style={{ width: '120px', height: '3px', backgroundColor: '#e74c3c', margin: '0 auto' }}></div>
          </div>

          {accountinfo.type === "super_admin" && 
            <div className='mt-4 text-center text-white' style={{ cursor: "pointer" }}>
             <Link to={"/account"}>  </Link>
            </div>
          }
        </div>
      

      </>
    }

    <Routes>
       {signed &&  <Route path="/" element={<Add />} />}
       {signed &&  <Route path="/account" element={<ACCOUNT />} />}
    </Routes>
  </div>
</Router>
  )
}


export default App;
